function cards() {
    // cards

    const menu = document.querySelector('.menu__field').firstElementChild;

    class Card {
        constructor(parentSelector, src, alt, subtitle, descr, price, ...classes) {
            this.parent = document.querySelector(parentSelector);
            this.src = src;
            this.alt = alt;
            this.subtitle = subtitle;
            this.descr = descr;
            this.price = price;
            this.classes = classes;
        }

        render() {
            const element = document.createElement('div');

            if (this.classes.length === 0) {
                this.classes = 'menu__item';
                element.classList.add(this.classes);
            } else {
                this.classes.forEach(className => element.classList.add(className));
            }
            
            element.innerHTML+=`
                <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.subtitle}</h3>
                <div class="menu__item-descr">${this.descr}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">:</div>
                    <div class="menu__item-total"><span>${this.price}</span> /</div>
                </div>
            `;
            this.parent.append(element);
        }
    }

    new Card('.menu .container', "img/tabs/vegy.jpg", "vegy", ' ""', ' "" -      :     .     .          !', 229).render();

    new Card('.menu .container', "img/tabs/elite.jpg", "elite",' ', '         ,     .  , ,  -      !', 550).render();

    new Card('.menu .container', "img/tabs/post.jpg", "post", ' ""', '  -    :     ,   , ,   ,          .', 430).render();    
}

export default cards;